import MultipleChoose from "./MultipleChoose";


const gridSytle = { marginLeft: 20, marginRight: 20 };

const styles = theme => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden',
        backgroundColor: theme.palette.background.paper,
    },

    heading: {
        fontSize: theme.typography.pxToRem(15),
        fontWeight: theme.typography.fontWeightRegular,
    },
    button: {
        margin: theme.spacing.unit,
    },
    leftIcon: {
        marginRight: theme.spacing.unit,
    },
    rightIcon: {
        marginLeft: theme.spacing.unit,
    },
    iconSmall: {
        fontSize: 20,
    },
    image: {
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    paper: {
        textAlign: 'justify',
        padding: theme.spacing.unit * 2,

    },
});

class Search extends Component {
    constructor() {
        super();
        this.state = {
            kereses: {
                fastText: "",
                reszletesKereses: false,
            },
            recipeIndToShow: [],
            open: [false, false],
            recipes: [],
            elkeszitesiIdo: "",
            elokeszitesiIdo: "",
            kaloria: "",
            evszak: [],
            unnep: [],
            dieta: [],
            menu: [],
            napszak: [],
            technika: [],
            hozzavalokCounter: 2,
            hozzavalok: [
                {
                    id: 1,
                    name: "",
                    counter: 1,
                    alapanyagok: [
                        getDefaultAlapanyag(0),
                    ]
                }
            ],
            elkeszites: {
                counter: 2,
                step: [
                    {
                        id: 1,
                        text: "",
                    }
                ]
            }
        };
    }

    componentDidMount() {
        this.getAllRecipe(this.props.token, this.props.user);
    }

    onSubmit = (e) => {
        e.preventDefault();


    };
    onFastTextChange(e) {
        let kereses = Object.assign([], this.state.kereses);
        kereses.fastText = e.target.value;
        this.setState({ kereses: kereses });
    }
    onReszletesChange(e) {
        let kereses = Object.assign([], this.state.kereses);
        kereses.reszletesKereses = e.target.checked;
        this.setState({ kereses: kereses });
    }

    onElkeszitesiIdoChange(e) {
        if (!(e.target.value < 0)) {
            this.setState({ elkeszitesiIdo: e.target.value });
        }
    }
    onElokeszitesiIdoChange(e) {
        if (!(e.target.value < 0)) {
            this.setState({ elokeszitesiIdo: e.target.value });
        }
    }
    onKaloriaChange(e) {
        if (!(e.target.value < 0)) {
            this.setState({ kaloria: e.target.value });
        }
    }
    onEvszakChange(label, e) {
        const evszak = this.state.evszak
        let index
        if (e.target.checked) {
            evszak.push(label)
        } else {
            index = evszak.indexOf(label)
            evszak.splice(index, 1)
        }
        evszak.sort();
        this.setState({ evszak: evszak })
    }
    onUnnepChange(label, e) {
        const unnep = this.state.unnep
        let index
        if (e.target.checked) {
            unnep.push(label)
        } else {
            index = unnep.indexOf(label)
            unnep.splice(index, 1)
        }
        unnep.sort();
        this.setState({ unnep: unnep })
    }
    onDietaChange(label, e) {
        const dieta = this.state.dieta
        let index
        if (e.target.checked) {
            dieta.push(label)
        } else {
            index = dieta.indexOf(label)
            dieta.splice(index, 1)
        }
        dieta.sort();
        this.setState({ dieta: dieta })
    }
    onMenuChange(label, e) {
        const menu = this.state.menu
        let index
        if (e.target.checked) {
            menu.push(label)
        } else {
            index = menu.indexOf(label)
            menu.splice(index, 1)
        }
        menu.sort();
        this.setState({ menu: menu })
    }
    onNapszakChange(label, e) {
        const napszak = this.state.napszak
        let index
        if (e.target.checked) {
            napszak.push(label)
        } else {
            index = napszak.indexOf(label)
            napszak.splice(index, 1)
        }
        napszak.sort();
        this.setState({ napszak: napszak })
    }
    onTechnikaChange(label, e) {
        const technika = this.state.technika
        let index
        if (e.target.checked) {
            technika.push(label)
        } else {
            index = technika.indexOf(label)
            technika.splice(index, 1)
        }
        technika.sort();
        this.setState({ technika: technika })
    }
    getAllRecipe(token, user) {
        axios.get(Constans.APIUrl + 'recipe/', {
            params: {
                token: token,
                user: user,
            }
        })
            .then(
                res => {
                    this.setState({ recipes: res.data });
                }

            )
            .catch((error) => {
            });
    };
    onRecipeClick(index) {
        var inds = this.state.recipeIndToShow;
        if (!inds.includes(index)) {
            inds.push(index);
            this.setState({
                recipeShow: true,
                recipeIndToShow: inds
            });
        }
        var myImage = new Image(100, 200);
        myImage.src = 'picture.jpg';


    }
    onClickCloseRecipe(e, index) {
        e.preventDefault();
        let recipeIndToShow = Object.assign([], this.state.recipeIndToShow);
        recipeIndToShow.splice(index, 1);
        this.setState({ recipeIndToShow: recipeIndToShow });
    }

    addSearch() {
        return (<form onSubmit={this.onSubmit.bind(this)}>
        <div >
            <FormControlLabel
                control={
                    <Switch
                        checked={this.state.kereses.reszletesKereses}
                        onChange={e => this.onReszletesChange(e)}

                    />
                }
                label="Részletes keresés"
            />
        </div>
        <div style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
            <NormalInput
                value={this.state.kereses.fastText}
                label="Gyors keresés"
                placeholder="Kifejezés amire keresni szeretnél..."
                width="200%"
                onChange={(e) => this.onFastTextChange(e)}
            />
        </div>
        <div style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
            <Button variant="contained" color="secondary" type="submit">
                Keresés
        </Button>
        </div>
    </form>);
    /*
        if (this.state.kereses.reszletesKereses) {
            return (<form onSubmit={this.onSubmit.bind(this)}>
                <div >
                    <FormControlLabel
                        control={
                            <Switch
                                checked={this.state.kereses.reszletesKereses}
                                onChange={e => this.onReszletesChange(e)}

                            />
                        }
                        label="Részletes keresés"
                    />
                </div>
                <div style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
                    <NormalInput
                        value={this.state.kereses.fastText}
                        label="Gyors keresés"
                        placeholder="Kifejezés amire keresni szeretnél..."
                        width="200%"
                        onChange={(e) => this.onFastTextChange(e)}
                    />
                </div>

                <Chip
                    label="Deletable Secondary Chip"
                    // onDelete={handleDelete}
                    //className={classes.chip}
                    color="secondary"
                />

                <Grid item xs={12} style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>

                </Grid>

                <Grid item xs={12} style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
                    <MultipleChoose checks={this.state.evszak} onChange={(label, e) => this.onEvszakChange(label, e)} title="Évszak:" labels={Constans.evszakLabels} />
                </Grid>
                <Grid item xs={12} style={gridSytle}>
                    <MultipleChoose checks={this.state.unnep} onChange={(label, e) => this.onUnnepChange(label, e)} title="Ünnepek:" labels={Constans.unnepekLabels} />
                </Grid>
                <Grid item xs={12} style={gridSytle}>
                    <MultipleChoose checks={this.state.dieta} onChange={(label, e) => this.onDietaChange(label, e)} title="Diéta:" labels={Constans.dietaLabels} />
                </Grid>
                <Grid item xs={12} style={gridSytle}>
                    <MultipleChoose checks={this.state.menu} onChange={(label, e) => this.onMenuChange(label, e)} title="Menü:" labels={Constans.menuLabels} />
                </Grid>
                <Grid item xs={12} style={gridSytle}>
                    <MultipleChoose checks={this.state.napszak} onChange={(label, e) => this.onNapszakChange(label, e)} title="Napszak:" labels={Constans.napszakLabels} />
                </Grid>
                <Grid item xs={12} style={gridSytle}>
                    <MultipleChoose checks={this.state.technika} onChange={(label, e) => this.onTechnikaChange(label, e)} title="Technika:" labels={Constans.technikaLabels} />
                </Grid>
                <Grid item xs={12} style={gridSytle}>

                    <FormGroup row={true}>
                        <NormalInput width="90%" margin="normal" adorment="perc" type="number" value={this.state.elkeszitesiIdo} onChange={this.onElkeszitesiIdoChange.bind(this)} label="Elkészítési idő (perc)" placeholder="Elkészítési idő" required={true} />
                        <NormalInput width="90%" margin="normal" adorment="perc" type="number" value={this.state.elokeszitesiIdo} onChange={this.onElokeszitesiIdoChange.bind(this)} label="Előkészítési idő (perc)" placeholder="Előkészítési idő" required={true} />

                    </FormGroup>
                </Grid>
                <Grid item xs={12} style={gridSytle}>
                    <NormalInput adorment="kcal" type="number" value={this.state.kaloria} onChange={this.onKaloriaChange.bind(this)} label="Kalória" placeholder="Kalória" required={true} width='100%' margin="normal" />
                </Grid>












                <div style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
                    <Button variant="contained" color="secondary" type="submit">
                        Keresés
            </Button>
                </div>
            </form>);
        } else {
            return (<form onSubmit={this.onSubmit.bind(this)}>
                <div >
                    <FormControlLabel
                        control={
                            <Switch
                                checked={this.state.kereses.reszletesKereses}
                                onChange={e => this.onReszletesChange(e)}

                            />
                        }
                        label="Részletes keresés"
                    />
                </div>
                <div style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
                    <NormalInput
                        value={this.state.kereses.fastText}
                        label="Gyors keresés"
                        placeholder="Kifejezés amire keresni szeretnél..."
                        width="200%"
                        onChange={(e) => this.onFastTextChange(e)}
                    />
                </div>
                <div style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
                    <Button variant="contained" color="secondary" type="submit">
                        Keresés
                </Button>
                </div>
            </form>);
        }
        */
    }


    render() {
        const { classes } = this.props;
        const val = 'auto'
        return (

            <div>
                <ExpansionPanel expanded={this.state.open[0]} onChange={(e, o) => this.setState({ open: [!this.state.open[0], this.state.open[1]] })}>
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>

                        <Typography className={classes.heading}>Keresés</Typography>
                    </ExpansionPanelSummary>

                    <ExpansionPanelDetails>


                        {this.addSearch()}


                    </ExpansionPanelDetails>
                </ExpansionPanel>
                <ExpansionPanel expanded={this.state.open[1]} onChange={(e, o) => this.setState({ open: [this.state.open[0], !this.state.open[1]] })}>
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                        <Typography className={classes.heading}>Keresési eredmények</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails>
                        <div className={classes.root}>

                            <Grid container spacing={24}>
                                {
                                    this.state.recipes.map((recipe, index) => {
                                        let str = "";
                                        recipe.elkeszites.step.map((step) => {
                                            str = str + step.text + " ";
                                            return str;
                                        });

                                        return (
                                            <Grid item xs={val} key={recipe._id} onClick={() => this.onRecipeClick(index)}>
                                                <RecipeCard imagePath={recipe.fileUrl}
                                                   editClick= {(e)=>{this.props.recipeEditClick(e, recipe)}}
                                                    recipeTitle={recipe.receptCim}
                                                    details={str.substr(0, Math.min(200, str.length))} />
                                            </Grid>
                                        );
                                    })
                                }
                            </Grid>
                        </div>
                    </ExpansionPanelDetails>
                </ExpansionPanel>


                {
                    this.state.recipeIndToShow.map((recipeInd, index) => {
                        const recipe = this.state.recipes[recipeInd];
                        return (

                            <ExpansionPanel key={recipe._id} onDoubleClick={(e) => this.onClickCloseRecipe(e, index)}>
                                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>

                                    <Typography className={classes.heading}>{recipe.receptCim}</Typography>

                                </ExpansionPanelSummary>
                                <ExpansionPanelDetails>


                                    <RecipePage state={recipe} part={1} />


                                </ExpansionPanelDetails>
                            </ExpansionPanel>
                        )
                    })
                }

            </div>
        )
    }
}

Search.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Search);
