import React, { Component } from "react";
import Create from './components/create.component';
import Edit from './components/edit.component';
import Search from './components/search.component';
import Login from './components/login.component';

import 'bootstrap/dist/css/bootstrap.min.css';

import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import CssBaseline from '@material-ui/core/CssBaseline';
import Divider from '@material-ui/core/Divider';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';
import InputIcon from '@material-ui/icons/Input';
import PowerSettingIcon from '@material-ui/icons/PowerSettingsNew';
import CustomDialog from './components/CustomDialog';


import * as Constans from './Constans';
import axios from 'axios';

import { getDefaultAlapanyag, getDefaultHozzavalo } from "./components/Hozzavalok.js"



import { withStyles, MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import classNames from 'classnames';

const drawerWidth = 240;
const styles = theme => ({
  root: {
    display: 'flex',
  },
  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginLeft: 12,
    marginRight: 20,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: '0 8px',
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 3,
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
});
const theme = createMuiTheme({
  palette: {
    primary: { main: '#2E3B55' }, // Purple and green play nicely together.
    secondary: { main: '#FD6A02' }, // This is just green.A700 as hex.
  },
  typography: { useNextVariants: true },
});

const ujRecept = 'Új recept';
const kereses = 'Keresés';
const szerkeztes = 'Szerkeztés';
export const login = 'Bejelentkezés';
const recipeDBToken = 'recipeDBToken';
const recipeDBUSer = 'recipeDBUSer';
export const  stateUjRecept = {
  tag:"",
  numberOfPeople: "",
  file: "",
  fileUrl: "",
  fileID: "",
  receptCim: "",
  elkeszitesiIdo: "",
  elokeszitesiIdo: 0,
  kaloria: 0,
  
  evszak: [],
  unnep: [],
  dieta: [],
  menu: [],
  napszak: [],
  technika: [],
  hozzavalokCounter: 2,
  hozzavalok: [
      {
          id: 1,
          name: "",
          counter: 1,
          alapanyagok: [
              getDefaultAlapanyag(0),
          ]
      }
  ],
  elkeszites: {
      counter: 2,
      step: [
          {
              id: 1,
              text: "",
          }
      ]
  }

};


function getChoosenComponenth(inp) {
  
  const actTab = inp.state.actTab;
  if (inp.state.loggedIn) {
    if (actTab.localeCompare(ujRecept) === 0) {
      
      return (
        <Create token={inp.state.token} user={inp.state.user} state={stateUjRecept} edit={false}/>
      );
    } else if (actTab.localeCompare(szerkeztes) === 0) {
      return (
        <Create token={inp.state.token} user={inp.state.user} state={inp.state.recipeToEdit}  edit={true} />
      );
    } else if (actTab.localeCompare(kereses) === 0) {
      return (
        <Search token={inp.state.token} user={inp.state.user} recipeEditClick = {(e, recipe)=> inp.recipeEditClick(e, recipe)}/>
      );
    }
  } else {
    return (
      <Login onSubmit={(event) => inp.login(event)} psw={inp.state.psw} user={inp.state.user} checked={inp.state.remember}
      rememberChange = {e=> inp.rememberChange(e)}
      userChange = {e=> inp.userChange(e)}
      pswChange = {e=> inp.pswChange(e)}/>
    );
  }
}


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      loggedIn: false,
      actTab: login,
      token: "",
      dialogMsg: "Rossz felhasználónév vagy jelszó",
      dialogOpen: false,
      okMsg: "Ok",
      progValue: 100,
      user: "",
      psw: "",
      remember: false,
      recipeToEdit: stateUjRecept
    };

    this.loginNecessary(kereses);
  }

  userChange = (e) => {
    this.setState({ user: e.target.value })
  };
  pswChange = (e) => {
    this.setState({ psw: e.target.value })
  };
  rememberChange = (e) => {
    this.setState({ remember: e.target.checked })
  };

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };
  onItemClick = (item) => {
      this.setState({
        actTab: item,
        open: false
      });
  };

  getChoosenComponent() {
  
    const actTab = this.state.actTab;
    if (this.state.loggedIn) {
      if (actTab.localeCompare(ujRecept) === 0) {
        
        return (
          <Create token={this.state.token} user={this.state.user} state={stateUjRecept} edit={false}/>
        );
      } else if (actTab.localeCompare(szerkeztes) === 0) {
        return (
         // <Create token={this.state.token} user={this.state.user} state={this.state.recipeToEdit}  edit={true} />
         <Edit token={this.state.token} user={this.state.user} state={this.state.recipeToEdit}  edit={true} />
        );
      } else if (actTab.localeCompare(kereses) === 0) {
        return (
          <Search token={this.state.token} user={this.state.user} recipeEditClick = {(e, recipe)=> this.recipeEditClick(e, recipe)}/>
        );
      }
    } else {
      return (
        <Login onSubmit={(event) => this.login(event)} psw={this.state.psw} user={this.state.user} checked={this.state.remember}
        rememberChange = {e=> this.rememberChange(e)}
        userChange = {e=> this.userChange(e)}
        pswChange = {e=> this.pswChange(e)}/>
      );
    }
  }

  login = (event) => {
    event.preventDefault();
    const obj = {
      user: this.state.user,
      psw: this.state.psw,
    };
    axios.post(Constans.APIUrl + 'recipe/login', obj)
      .then(
        res => {
          this.setState({
            token: res.data.token,
            loggedIn: true,
            actTab: kereses
          })
          if (this.state.remember) {
            localStorage.setItem(recipeDBToken, res.data.token);
            localStorage.setItem(recipeDBUSer, this.state.user);
          }
        }

      )
      .catch((error) => {
        this.setState({
          loggedIn: false,
          dialogOpen: true,
          user: "",
          psw: "",
          remember: false
        });
      });

  };

  loggingIcon = () => {
    if (!this.state.loggedIn) {
      return (
        <ListItem button key='Bejelentkezés' onClick={this.onItemClick.bind(this, login)}>
          <ListItemIcon><InputIcon /></ListItemIcon>
          <ListItemText primary='Bejelentkezés' />
        </ListItem>);
    } else {
      return (
        <ListItem button key='Kijelentkezés' onClick={this.logout}>
          <ListItemIcon><PowerSettingIcon /></ListItemIcon>
          <ListItemText primary='Kijelentkezés' />
        </ListItem>
      )
    }
  }

  loginNecessary = (tab) => {

    if (this.state.loggedIn) {
      this.setState({ open: false });
      this.onItemClick.bind(this, tab)
    } else {
      const token = localStorage.getItem(recipeDBToken);
      const user = localStorage.getItem(recipeDBUSer);
      const obj = {
        user: user,
        token: token,
      };
      
      axios.post(Constans.APIUrl + 'recipe/token', obj)
        .then(
          res => {
            this.setState({
              token: token,
              loggedIn: true,
              actTab: tab,
              open: false,
              user: user,
            })
        
          }

        )
        .catch((error) => {
          this.setState({
            open: false,
            actTab: login
          });
        });

    }
  };

  logout = () => {
    this.setState({
      token: "",
      loggedIn: false,
      actTab: login,
      open: false,
      user: "",
      psw: "",
    })
    localStorage.setItem(recipeDBToken, "");
    localStorage.setItem(recipeDBUSer, "");

  };

  handleDialogClose = () => {
    this.setState({ dialogOpen: false })
  }

  recipeEditClick(e, recipe){
    e.preventDefault();

    let elkeszites = recipe.elkeszites;
    let step = elkeszites.step;
    for(var i=0; i< step.length; i++){
        step[i] = {id: i,
                  text: step[i].text};
    }
    elkeszites = {
      step: step,
      counter: step.length
    }
    recipe.elkeszites = elkeszites;
    recipe.hozzavalokCounter = recipe.hozzavalok.length + 1;

    let hozzavalok = recipe.hozzavalok;
    for(var i=0; i< hozzavalok.length; i++){
      for(var j=0; j< hozzavalok[i].alapanyagok.length; j++){
        hozzavalok[i].alapanyagok[j] = {
          id: j,
          name: hozzavalok[i].alapanyagok[j].name,
          mennyiseg: hozzavalok[i].alapanyagok[j].mennyiseg,
          mertekegyseg: hozzavalok[i].alapanyagok[j].mertekegyseg,
          kaloria: hozzavalok[i].alapanyagok[j].kaloria,
        };
      }
      hozzavalok[i] = {id: i+1,
                name: hozzavalok[i].name,
              counter: hozzavalok[i].alapanyagok.length,
            alapanyagok: hozzavalok[i].alapanyagok};
  }

recipe.hozzavalok = hozzavalok;
    this.setState({
      recipeToEdit: recipe,
      open: false,
    });
    this.onItemClick(szerkeztes)
  }


  render() {
    const { classes } = this.props;
    const { open } = this.state;

    return (

      <MuiThemeProvider theme={theme}>
        <CustomDialog dialogOpen={this.state.dialogOpen}
          dialogMsg={this.state.dialogMsg}
          onClose={this.handleDialogClose.bind(this)}
          okMsg={this.state.okMsg}
          progValue={this.state.progValue} />
        <div className={classes.root}>
          <CssBaseline />
          <AppBar
            position="fixed"
            className={classNames(classes.appBar, {
              [classes.appBarShift]: open,
            })}
          >
            <Toolbar disableGutters={!open}>
              <IconButton
                color="inherit"
                aria-label="Open drawer"
                onClick={this.handleDrawerOpen}
                className={classNames(classes.menuButton, open && classes.hide)}
              >
                <MenuIcon />
              </IconButton>
              <Typography variant="h6" color="inherit" noWrap>
                {this.state.actTab}
              </Typography>
            </Toolbar>
          </AppBar>
          <Drawer
            className={classes.drawer}
            variant="persistent"
            anchor="left"
            open={open}
            classes={{
              paper: classes.drawerPaper,
            }}
          >
            <div className={classes.drawerHeader}>
              <IconButton onClick={this.handleDrawerClose}>
                {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
              </IconButton>
            </div>
            <Divider />
            <List>
              <ListItem key={kereses} onClick={this.onItemClick.bind(this, kereses)} button>
                <ListItemIcon><SearchIcon /></ListItemIcon>
                <ListItemText primary={kereses} />
              </ListItem>
              
              <ListItem key={ujRecept} onClick={this.onItemClick.bind(this, ujRecept)} button>
                <ListItemIcon><AddIcon /></ListItemIcon>
                <ListItemText primary={ujRecept} />
              </ListItem>
              <Divider />
              {this.loggingIcon()}
            </List>
          </Drawer>

          <main
            onClick={() => this.setState({ open: false })}
            className={classNames(classes.content, {
              [classes.contentShift]: open,
            })}
          >
            <div className={classes.drawerHeader} />
            {this.getChoosenComponent()}
          </main>

        </div>
      </MuiThemeProvider>
    );
  }
}

App.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(App);

